<template>
    <page-container>
        <h3 class="mb-4">Items Database </h3>
        <div class="mb-2">WARNING! Currently running in <u class="text-danger">(Edit Mode)</u>!</div>

        <b-button-group size="sm" class="mb-3">
            <b-button variant="info">All</b-button>
            <b-button variant="info">Weapons</b-button>
            <b-button variant="info">Armors</b-button>
            <b-button variant="info">Daemons</b-button>
            <b-button variant="info">Consumables</b-button>
            <b-button variant="info">Appraisals</b-button>
        </b-button-group>

        <div v-masonry transition-duration="0" item-selector=".item" class="masonry-container">
            <div v-masonry-tile class="item" :key="item" v-for="(item, index) in items"
                 :class="{'has-value': itemsValues[item] !== undefined, 'value-approved': typeof itemsValues[item] === 'string'}">
                <img :id="`item-${item}`" :src="getItemImg(item)">
                <b-tooltip :show.sync="showItems[item]" :target="`item-${item}`" :no-fade="true" placement="right"
                           delay="0" triggers="hover click" @show="hideOtherTooltips(item)">
                    <div>
                        <div v-if="typeof itemsValues[item] !=='string'">
                            <div>
                                <img :src="getItemImg(item)" width="70">
                            </div>
                            <h5>What is the name of this item?</h5>
                            <input type="text" @keyup.enter.prevent="saveItemName(item, $event)" list="itemdesc"
                                   autocomplete="off">
                        </div>
                        <div v-else>
                            <b-container class="item-info">
                                <b-row no-gutters>
                                    <b-col :cols="12" class="item-name">{{ itemsinfo[item].name }}</b-col>
                                </b-row>
                                <b-row no-gutters>
                                    <b-col :cols="3" class="item-img"><img :src="getItemImg(item)" width="70"></b-col>
                                    <b-col class="item-stat">
                                        <div>Type:</div>
                                        <div>
                                            <div class="d-inline-block" style="min-width: 50px">Price: P{{
                                                itemsinfo[item].price }}
                                            </div>
                                            <div class="d-inline-block" style="margin-left: 10px">Weight: {{
                                                itemsinfo[item].lvlreq }}
                                            </div>
                                        </div>
                                        <div>Sell Price: P{{ Math.floor(itemsinfo[item].price / 2) }}</div>
                                        <div>Req'd Class: {{ itemsinfo[item].classreq || 'General' }}</div>
                                        <div>Required Level: {{ itemsinfo[item].weight || '0' }}</div>
                                    </b-col>
                                </b-row>
                                <b-row no-gutters>
                                    <b-col :cols="12" class="item-desc">
                                        <div>
                                            <p  class="m-0 p-0" v-html="descOrCol2(item) && descOrCol2(item).replace(/\\n/gi, '<br>') || '<em>No description.</em>'"></p>
                                            <template v-if="itemsinfo[item].stats && typeof itemsinfo[item].stats === 'string'">
                                                <p class="ml-1 mt-2 mb-1 text-danger">
                                                    <strong>Effects:</strong>
                                                </p>
                                                <p class="ml-2 text-danger" v-html="formatStats(itemsinfo[item].stats)"></p>
                                            </template>
                                        </div>
                                    </b-col>
                                </b-row>
                            </b-container>
                        </div>
                    </div>
                </b-tooltip>
            </div>
        </div>

        <div class="text-center mt-4">
            <b-pagination-nav class="d-inline-flex" variant="info" :link-gen="linkGen" :number-of-pages="Math.ceil(itemscount / perpage)" use-router></b-pagination-nav>
        </div>

        <datalist id="itemdesc">
            <option v-for="item in itemdesc">{{ item.name }}</option>
        </datalist>
    </page-container>
</template>

<script>

    import items from '../assets/items.files'
    import itemids from '../assets/items.id'
    import itemdesc from '../assets/items'
    import Case from 'case'

    import _ from 'lodash'

    export default {
        data() {
            return {
                perpage    : 100,
                user       : "",
                itemdesc,
                itemscount : items.length,
                db         : fbapp.database(),
                showItems  : items.reduce((obj, val) => {
                    obj[val] = false;
                    return obj;
                }, {}),
                itemsValues: itemids
            }
        },
        computed: {
            items() {
                let pointer = (this.currentpage - 1) * this.perpage;
                return items.filter(item => item !== 161).slice(pointer, pointer + 100);
            },
            currentpage() {
                return this.$route.query.page || 1;
            },
            itemsinfo() {

                let info = {};

                Object.keys(this.itemsValues).forEach((id) => {

                    if (typeof this.itemsValues[id] === 'string' && id != 161) {
                        info[id] = _.find(this.itemdesc, ['name', this.itemsValues[id]])
                    }

                });

                return info;
            }
        },
        mounted() {
            this.user = prompt("Please enter your name");

            this.db.ref('/items').on('value', (snapshot) => {
                let val = snapshot.val();

                console.log(val)

                for (let id in val) {
                    if (val.hasOwnProperty(id)) {

                        let v = val[id];

                        if (typeof v === 'string') this.$set(this.itemsValues, id, v);
                        else if (typeof v === 'object') {


                            let ssr = [];

                            for (let ss in v) {
                                if (v.hasOwnProperty(ss)) {
                                    ssr.push(v[ss])
                                }
                            }

                            this.$set(this.itemsValues, id, ssr);

                        }

                    }
                }
            });


        },
        beforeDestroy() {
            this.db.ref('/items').off('value');
        },
        methods : {
            formatStats(stats) {
                return stats.split(' ').map((stat) => {

                    let [label, value] = stat.split('=');

                    let keywords = [
                        'slash_chance',
                        'duplicate_chance',
                        'chance',
                        'plus',
                        'effect_percent',
                        'percent',
                        'duration',
                        'heal_life',
                        'regen_life',
                        'daemon_damage',
                        'regen_weight',
                        'damage',
                        'fortune',
                        'cookie',
                        'sweetcane',
                        'cane',
                        'boost'
                    ]

                    let match;


                    if (match = label.match(/(.+)_slash_chance$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: trigger chance +${value}%`;
                    }
                    if (match = label.match(/(.+)_duplicate_chance$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: trigger chance +${value}% `;
                    }
                    if (match = label.match(/(.+)_chance$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: +${value}% chance`;
                    }
                    if (match = label.match(/(.+)_plus$/i)) {

                        if (match[1] === 'flee') match[1] = 'dodge';

                        if (match[1] === 'defense') {
                            value = (value * 0.39) + '%';
                        }

                        return `<strong>${Case.capital(match[1])}</strong>: +${value}`;
                    }


                    if (match = label.match(/(.+)_effect_percent$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: +${value}%`;
                    }

                    if (match = label.match(/(.+)_percent$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: +${value}%`;
                    }
                    if (match = label.match(/(.+)_duration$/i)) {
                        return `<strong>${Case.capital(match[1])}</strong>: +${value} seconds duration`;
                    }

                    if (label === 'heal_life') {
                        return `<strong>HP Restore</strong>: +${value}`;
                    }
                    if (label === 'regen_life') {
                        return `<strong>HP Regen</strong>: +${value}`;
                    }

                    if (label === 'daemon_damage') {
                        return undefined;
                    }


                }).filter(v => v !== undefined).join('<br>')
            },
            getItemImg(pet) {
                let images = require.context('../assets/items', false, /\.png$/);
                return images('./' + pet + ".png")
            },
            descOrCol2(item) {
                return this.itemsinfo[item].desc || this.itemsinfo[item].unknownCol2 || undefined;
            },
            saveItemName(item, event) {

                if (this.user === 'wisdomsky') {
                    this.db.ref('items/' + item).set(event.target.value);
                } else {

                    this.db.ref('items/' + item).push({
                                                          name: event.target.value,
                                                          user: this.user
                                                      });

                    alert('Thank you for your contribution! :)')
                }




                this.showItems[item] = false;
            },
            hideOtherTooltips(item) {
                for (let k in this.showItems) {
                    if (this.showItems.hasOwnProperty(k) && this.showItems[k] && k !== item) this.showItems[k] = false;
                }


            },
            linkGen(pageNum) {
                return pageNum === 1 ? '?' : `?page=${pageNum}`
            }
        }
    }
</script>

<style lang="scss" scoped>
    .item {
        margin: 8px;
        box-sizing: border-box;
        opacity: 0.2;

        &.has-value {
            /*border: 3px #f22 solid;*/
            opacity: 1;
            border: 1px solid #f00;
        }


        &.value-approved {
            border: 0;
        }

        img {
            height: 40px;
            width: 40px;
        }

        &:hover {
            transform: scale(2);
        }

    }

    .item-info {
        margin: 0;
        padding: 0;
        min-width: 300px;

        .item-name {
            text-align: left;
            color: rgb(255, 255, 8);
            font-weight: bold;
            background-color: rgb(28, 28, 28);
            margin-bottom: 2px;
            padding-left: 5px;
        }

        .item-img {
            border: 3px solid rgb(28, 28, 28);
            border-radius: 5px;
            margin-left: 2px;
        }

        .item-stat {
            border: 3px solid rgb(28, 28, 28);
            border-radius: 5px;
            text-align: left;
            font-size: 11px;
            padding-left: 5px;
            margin-left: 2px;
            margin-right: 2px;
        }

        .item-desc div {
            border: 3px solid rgb(84, 64, 16);
            font-size: 11px;
            border-radius: 5px;
            margin-left: 4px;
            margin-right: 6px;
            margin-top: 2px;
            padding: 5px 3px;
            text-align: left;
        }

        & /deep/ .row {

        }

    }


</style>

<style>
    .tooltip-inner {
        max-width: 300px !important;
        padding: 0 !important;
        border: 3px solid rgb(28, 28, 28);
        overflow-x: hidden !important;
    }
</style>